<template>
	<div class="register-block">
	<transition name="slide-left">
		<div class="child-view" v-if="loginChcek">
			<h1 class="reg-title"></h1>
			<div class="form-list">
				<span class="contry-code fl">+86</span>
				<div class="bfc">
					<input type="tel" v-model="username" class="reg-input" placeholder="">
				</div>

			</div>
			<div class="form-list">
				<input type="password" v-model="password" class="reg-input" id="pwd" placeholder=" ">
			</div>
			<span class="reg-btn" @click="login(1)"></span>
			<span class="reg-btn reg-btn-red" @click="loginChcek = !loginChcek"></span>
			<div class="ta-r">
			<router-link :to="{ name: 'register'}" class="color-white"></router-link>
			<router-link :to="{ name: 'forgetpwd'}" class="fl color-white"></router-link>
			</div>
		</div>
	</transition>
	<transition name="slide-right">
    <div class="child-view" v-if="!loginChcek">
      <h1 class="reg-title"></h1>
      <div class="form-list">
        <span class="contry-code fl">+86</span>
        <div class="bfc">
          <input type="tel" v-model="username" class="reg-input" placeholder="">
        </div>

      </div>
      <div class="form-list">
        <input type="tel" v-model="smsCode" class="reg-input" id="pwd" placeholder="">
        <span class="get-code" @click="getSmsCode()" :class="{'disabled': loginState == true}">{{getCodeTxt}}</span>
      </div>
      <span class="reg-btn" @click="login(2)"></span>
      <span class="reg-btn reg-btn-red" @click="loginChcek = !loginChcek"></span>
      <div class="ta-r">
        <router-link :to="{ name: 'register'}" class="color-white"></router-link>
        <router-link :to="{ name: 'forgetpwd'}" class="fl color-white"></router-link>
      </div>
    </div>
	</transition>
	</div>
</template>

<script>
  import Toast from 'vue-toast-mobile';

  export default {
    data () {
      return {
			username: '',
			password: '',
			username2: '',
			smsCode: '',
			loginChcek: true,
			getCodeTxt: '',
			getSmsState: false,
			sendcode: '',
			second: 60,
			loginState: false
		}
	},
	methods: {
  		async login(type) {
    		let self = this;
    		let mobileReg = this.$appConfig.verify(this.username,'mobile',true);

			if(mobileReg){
				Toast({
				  message: mobileReg,
				});
				return false;
			}
			let emptyReg = null;
			let emptyTxt = null;
			if(type == 1){
				emptyReg = self.password;
				emptyTxt = ''
			}else{
				emptyReg = this.smsCode;
				emptyTxt = ''
			}
			if(!emptyReg){
				Toast({
				  message: emptyTxt,
				});
				return false;
			}
	          let data = new FormData()
	          data.append('type', type);
	          data.append('username', this.username);
	          type == 1 ? data.append('password', this.password) : data.append('code', this.smsCode);
	
	          const loginAjax = await fetch(this.$appConfig.api.login, {
	            method: 'POST',
	            body: data
	          });
	          let userData = await loginAjax.json();
	          if(userData.code == '200'){
	          	//  
	            this.$store.dispatch('UPDATE_USERINFO', userData.datas);
	            //  localStorage
	            localStorage.setItem('userinfo', JSON.stringify(userData.datas))
	            // 
	            let redirect = decodeURIComponent(this.$route.query.redirect || '/');
	            self.$router.push({
	              path: redirect
	            });
			}else{
	            Toast(userData.datas.error);
			}

		},
		async getSmsCode () {
			let result = this.$appConfig.verify(this.username,'mobile',true);
			if(result){
				Toast(result);
				return false;
			}
			let data = new FormData();
			data.append('username', this.username);
			const getCodeAjax = await fetch(this.$appConfig.api.getSmsCode, {
	            method: 'POST',
	            body: data
	        });
	        let response = await getCodeAjax.json();
	        if(response.code == '200'){
	            Toast(response.datas.msg);
					this.getSmsState = true;
					this.time()
					this.sendcode = response.datas.sendcode;
					this.loginState = true
			}else{
	            Toast(response.datas.error);
			}
			
		},
		time (){
	      let self = this;
	      var timer = setInterval(function(){
	      self.second--
	       self.getCodeTxt = '(' + self.second + ')'
	        if(self.second == 0){
	          clearInterval(timer);
	          self.getSmsState = false
	          self.getCodeTxt = ''
	          self.loginState = false
	          self.second = 60
	        }
	      },1000)
	    }
	},
	mounted () {
		this.$store.commit('UPDATE_LOADING', false);
	}
  }
</script>



